        transform: translateY(-1700px)rotate(600deg)
    }
}

@keyframes square {
    0% {
        -webkit-transform: translateY(0);
        transform: translateY(0)
    }
    100% {
        -webkit-transform: translateY(-1700px)rotate(600deg);
        transform: translateY(-1700px)rotate(600deg)
    }
}

